<template>
  <div class="cardInvoice" ref="cardInvoice" :style="{height: cardInvoiceHeigh}">
    <van-form>
      <div class="title"></div>

      <van-cell title="" title-class="van-field__label" value-class="van-field__value">
        <van-radio-group v-model="radio" direction="horizontal">
          <van-radio name="1" icon-size="18px"></van-radio>
          <!-- <van-radio name="2" icon-size="18px"></van-radio> -->
        </van-radio-group>
      </van-cell>

      <van-field
        v-model="invoiceHead"
        name=""
        label=""
        placeholder=""
      />
      <van-field
        v-model="invoiceCode"
        name=""
        label=""
        placeholder=""
      />

      <div class="hr"></div>

      <van-field
        v-model="orderNo"
        name=""
        label=""
        readonly
      />
      <!-- <van-field
        v-model="orderMoney"
        name=""
        label=""
        readonly
      /> -->
      <!-- <van-field
        v-model="orderType"
        name=""
        label=""
        readonly
      /> -->
      <div class="title"></div>
      <van-field
        v-model="applyName"
        name=""
        label=""
        placeholder=""
        @click="resizeSroll($event)"
      />
      <van-field
        v-model="applyPhone"
        name=""
        label=""
        placeholder=""
        @click="resizeSroll($event)"
        type="tel"
      />
      <van-field
        v-model="applyEmail"
        name=""
        label=""
        placeholder=""
        @click="resizeSroll($event)"
      />
      <!-- <van-field
        v-model="areaData"
        @click="showArea=true"
        name=""
        label=""
        placeholder=""
        readonly
      />
      <van-field
        v-model="applyAreaData2"
        name=""
        label=""
        placeholder=""
      /> -->

      <!-- <van-field
        v-show="showBank"
        v-model="applyBank"
        name=""
        label=""
        placeholder=""
      />
      <van-field
        v-show="showBank"
        v-model="applyBankNum"
        name=""
        label=""
        placeholder=""
      /> -->

    </van-form>
    <div class="btnSubmit" ref="btnSubmit">
      <van-button :disabled="btnDisabled"  block type="info" native-type="submit" :color="btnColor" @click="invoiceApply()">
        
      </van-button>
    </div>

    <van-popup v-model="showArea" position="bottom" :overlay="true">
      <van-area :area-list="areaList" @cancel="showArea=false" />
    </van-popup>
  </div>
</template>

<script>
import { Form, Field, RadioGroup, Radio, Cell, AddressEdit, Popup, Area, Button, Notify, Dialog  } from 'vant';
import areaList from "../../assets/js/areaData.js"
import { getStorage } from "../../utilies";
import { _post } from '../../http/index.js';
export default {
  name: 'card_invoice',
  components: {
    [Form.name]: Form,
    [Field.name]: Field,
    [RadioGroup.name]: RadioGroup,
    [Radio.name]: Radio,
    [Cell.name]: Cell,
    [AddressEdit.name]: AddressEdit,
    [Popup.name]: Popup,
    [Area.name]: Area,
    [Button.name]: Button,
    [Notify.name]: Notify,
  },
  data() {
    return {
      radio: '1',
      invoiceHead: '',
      invoiceCode: '',
      orderNo: '',
      orderMoney: '',
      orderType: '',
      applyName: '',
      applyPhone: '',
      applyEmail: '',

      areaData: '',
      applyAreaData2: '',

      applyBank: '',
      applyBankNum: '',

      areaList,
      showArea: false,

      cardInvoiceHeigh: '',
      btnDisabled: true,
    };
  },
  computed: {
    showBank() {
      return this.radio == 1
    },
    btnColor() {
      if (this.invoiceHead && this.applyName && this.applyPhone && this.applyEmail){
        this.btnDisabled = false
        return '#1989fa'
      } else {
        this.btnDisabled = true
        return '#E0E0E0'
      }
    }
  },
  created() {
    this.initial()
  },
  mounted() {

  },
  methods: {
    initial() {
      if (!this.$route.params.order) {
        Notify({
          message: ''
        })
        setTimeout( () => {
          history.go(-1)
        }, 2000)
    
        return
      }
      let order = this.$route.params.order
      this.orderNo = order.no
    },
    resizeSroll(e) {
      const _this = this
      const innerHeight = window.innerHeight
      let H_btnSubmit = this.$refs.btnSubmit.offsetHeight
      let H_cardInvoice = this.$refs.cardInvoice.clientHeight
      
      window.onresize = function () {
        const newInnerHeight = window.innerHeight;
        _this.$refs.cardInvoice.style.height = H_cardInvoice + Math.abs(newInnerHeight-e.y) + H_btnSubmit + 'px'
  
        if (innerHeight > newInnerHeight &&(Math.abs(newInnerHeight-e.y) + H_btnSubmit) < 200) {
          // 
          window.scroll(0,  Math.abs(newInnerHeight-e.y) + H_btnSubmit)
        } else {
          // 
          window.scroll(0, 0)
          _this.$refs.cardInvoice.style.height = H_cardInvoice + 'px'
        }
        

      }
    },
    invoiceApply() {
      if (!(/^(13[0-9]|14[579]|15[0-3,5-9]|16[6]|17[0135678]|18[0-9]|19[89])\d{8}$/).test(this.applyPhone)) {
        Notify({message: ""})
        return false
      }
      if (!( /^\w+@[a-zA-Z0-9]{2,10}(?:\.[a-z]{2,4}){1,3}$/).test(this.applyEmail)) {
        Notify({message: ""})
        return false
      }
      let userInfo = getStorage("userInfo", "obj")
      let params = {
        ordernum: this.orderNo,
        contacts: this.applyName.replace(/\s+/g,''),
        telphone: this.applyPhone,
        address: this.applyEmail.replace(/\s+/g,''),
        inv_head: this.invoiceHead.replace(/\s+/g,''),
        inv_ident_num: this.invoiceCode.replace(/\s+/g,''),
        user_id: userInfo?userInfo.account.user_id: 0
      }
      _post('/api/v1/app/orders/invoice_submits', {
        ...params
      }).then( (res) => {
        if (res.state == 1) {
          Notify({
            message: '1~3',
            background: '#4CAF50'
          })
          this.applyName = ''
          this.applyPhone = ''
          this.applyEmail = ''
          this.invoiceHead = ''
          this.invoiceCode = ''
        } else {
          Notify({
            message: res.msg
          })
        }
      })

    }
  }

}
</script>

<style lang="less" scoped>
  .cardInvoice {
    width: 100%;
    // height: 100%;
    // position: relative;
    .title {
      width: 100%;
      height: 55px;
      background: #E0E0E0;
      text-align: left;
      padding: 0 30px;
      font-size: 26px;
      letter-spacing: 2px;
      color: #9E9E9E;
      line-height: 55px;
      box-sizing: border-box;
      position: relative;
    }
    .hr {
      margin: 0;
      height: 8px;
      background: #E0E0E0;
    }
    .btnSubmit{
      width: 100%;
      padding: 16px 76px;
      position: fixed; 
      background: #fff;
      bottom: 0;
      left: 0;
      box-sizing: border-box;
    }
  }
  .van-address-edit {
    padding: 0!important;
  }
</style>